                </th>
                <td class="border-0 align-middle" style="max-width: 160px;">
                  <div class="input-group pe-5">
                    <div class="input-group-prepend">
                      <button class="btn btn-outline-dark border-0 py-2" type="button" id="button-addon1" @click="handleSub(item)" :disabled="item.id === status.loadingItem">
                        <i class="fas fa-minus"></i>
                      </button>
                    </div>
                    <input type="text" class="form-control border-0 text-center my-auto shadow-none" placeholder="" aria-label="Example text with button addon" aria-describedby="button-addon1" v-model.number="item.qty">
